@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  color: #212121;
}

.background {
  min-height: 100vh;
  width: 100%;
  background: #FEFEFE;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navbar {
    min-height: 10vh;
    width: 100%;
    background: #FEFEFE;
    display: flex;
    justify-content: space-around;
}


ul {
    display: flex;
}

ul li {
    list-style-type: none;
}

img {
    height: auto;
    width: 50px;
}
.container {
    min-height: 80vh;
    width: 70%;
    background: #FEFEFE;
    border-radius: 4px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.container ul {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
 
}


.start-container {
    min-height: 80vh;
    width: 70%;
    background: #FEFEFE;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.blockchain {
    width: 400px;
    height: auto;
}


.footer {
    min-height: 10vh;
    width: 100%;
    background: #FEFEFE;
}

